
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.content .item {
    height: 225px;
    width: 400px;
    position: relative;
    text-decoration: none;
    color: #000;
    background-color: #eee;
    margin: 5px 5px 5px 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.content .item .desc {
      position: absolute;
      height: 0;
      bottom: 0;
      width: 100%;
      transition: all .5s;
      overflow: hidden;
      background-color: #ccc;
}
.content .item:hover > .desc {
      height: 25%;
}



