
table {
  border-collapse: collapse;
  height: 600px;
}
table td {
    min-width: 100px;
}
table td.seat {
      border: 1px solid black;
}
table td.seat:hover {
        background-color: #ccc;
}
table td.seat.invalid {
        background-color: grey;
}
table td.seat.active {
        background-color: green;
}
table .screen {
    background-color: #efe;
}
.seats {
  padding-bottom: 60px;
}



