
.header {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.header ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 0;
}
.header ul .router-link-active > a {
      background-image: linear-gradient(to bottom, #dbdbdb 0, #e2e2e2 100%);
}
.header ul li a {
      background-image: linear-gradient(#fcf8e3 0%, #faf2cc 100%);
      text-decoration: none;
      transition: color .5s;
      color: #8a6d3b;
      padding: 10px 15px;
}
.header ul li a:hover {
        color: #454;
}
nav {
  display: -ms-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



